import React, { useState } from 'react';

const CommentsList = ({articleName,comments, setArticleInfo}) => {

    const [username, setUsername] = useState('');
    const [commentText, setCommentText] = useState('');

    const DeleteComment = async (c) => {
      
      
       
    
        console.log("hi" + commentText);
        const result = await fetch(`/api/articles/${articleName}/del-comment`, {
            method: 'post',
            body: JSON.stringify({ username, text: c }),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body = await result.json();
        setArticleInfo(body);
        setUsername('');
        setCommentText('');
    }
   
    return (
        
        <div id="add-comment-form" >
                      {comments.map((comment, key) =>(
     
     <div className="comment" key={key}>
         <h4  >{comment.username}:</h4>
         <p> {comment.text}</p>
         <button onClick={(event) => DeleteComment(comment.text)}>del Comment</button>

     </div>
 ))}
       
    
        </div>
        
    );
}




   


export default CommentsList;