import React, {useState,useEffect} from 'react';
import { useLocation} from "react-router-dom"
import ArticlesList from "../components/ArticlesList";
import CommentsList from "../components/CommentsList";
import UpvotesSection from "../components/UpvotesSection";
import articleContent from "./article-content";
import AddCommentForm from "../components/AddCommentForm";
import '../Loading.css';




const ArticlePage = () => {
    let location = useLocation();
    let c = location.pathname 
    let v = 0;
    let articleName = "";
    console.log(c.length);
    for(let x=0;x<c.length;x++ )
      { if (c[x] === "/")
          v++;
      if(v === 2)
      {
      if( c[x] === "/")
             continue;
     else
         articleName = articleName + c[x];
      }
}

const article = articleContent.find(article => article.name === articleName);

const[articleInfo, setArticleInfo] = useState({ upvotes: 0, comments:[]});


useEffect(() => {

    const fetchData = async () => {
            const result = await fetch(`/api/articles/${articleName}`)
            const body = await result.json();
   
            setArticleInfo(body);
    }
    fetchData();

}, [articleName]);

if(!article) return <h1>{articleName} doesn't exist</h1>


    const otherArticles = articleContent.filter(article => article.name !== articleName)

    return (
        <>
        <h1>{article.title}</h1>
        <UpvotesSection articleName={articleName} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
       
        {article.content.map((paragraph, key) => (
            <p key={key}>{paragraph}</p>
        ))}
        <CommentsList comments={articleInfo.comments} articleName={articleName} setArticleInfo={setArticleInfo}/>
        <AddCommentForm articleName={articleName} setArticleInfo={setArticleInfo} />

        <h3>Other Articles:</h3>
        <ArticlesList articles={otherArticles} />
        </>
    );
}

export default ArticlePage;