import React, { useState } from 'react';
import { FaSortDown } from 'react-icons/fa';
import { FaSortUp } from 'react-icons/fa';






const UpvotesSection = ({articleName,upvotes,setArticleInfo}) =>{
    const [loading, setLoading] = useState(false);
    const upvoteArticle = async () => {
        setLoading(true);
       const result =  await fetch(`/api/articles/${articleName}/upvote`, {
            method: "post",

        });
        const body = await result.json();
        setLoading(false);
        setArticleInfo(body);
    }
    
        const DownvoteArticle = async () => {
            setLoading(true);
           const result =  await fetch(`/api/articles/${articleName}/downvote`, {
                method: "post",
    
            });
            const body = await result.json();
            setLoading(false);
            setArticleInfo(body);
        }
        if (loading) return <h1>Loading...</h1>;
  return  (
        <div id="upvotes-section" >
            <div className="btn-group-vertical" >
        <button style={{padding:0}} onClick={() => upvoteArticle()}>  <FaSortUp /></button>
        <button className="button2" onClick={() => DownvoteArticle()}>  <FaSortDown /></button>
        </div>
        <p> this post has been upvotes {upvotes} times</p>
        </div>
    );

 }


export default UpvotesSection;